link: ''
},
{
name: '',
link: 'SalesOrderQuery'
},
{
name: '',
link: ''
},
{
name: '',
link: 'SalesReturnQuery'
},
{
name: '',
link: ''
}
]
},
{
name: '',
details: [
{
name: '',
link: 'AllocationListQuery'
},
{
name: '',
link: ''
},
{
name: '',
link: 'PurchaseInboundQuery'
},
{
name: '',
link: ''
},
{
name: '',
link: 'SalesOutboundQuery'
},
{
name: '',
link: ''
},
{
name: '',
link: 'OtherInboundQuery'
},
{
name: '',
link: ''
},
{
name: '',
link: 'OtherOutboundQuery'
},
{
name: '',
